<template>
  <section class="carousel" data-ride="carousel">
    <input type="radio" id="carousel-1" name="carousel[]" checked />
    <input type="radio" id="carousel-2" name="carousel[]" />
    <input type="radio" id="carousel-3" name="carousel[]" />
    <input type="radio" id="carousel-4" name="carousel[]" />
    <input type="radio" id="carousel-5" name="carousel[]" />
    <div class="container">
      <div class="carousel-caption relative">
        <h1>{{ title }}</h1>
        <h1>{{ sale }}</h1>
        <p v-html="details"></p>
        <a class="buyNow" href="#">Buy Now</a>
        <buttons-social />
      </div>
    </div>
    <ul class="carousel__items">
      <li class="carousel__item">
        <img :src="carouselImg1" alt="" />
      </li>
      <li class="carousel__item">
        <img :src="carouselImg2" alt="" />
      </li>
      <li class="carousel__item">
        <img :src="carouselImg3" alt="" />
      </li>
      <li class="carousel__item">
        <img :src="carouselImg4" alt="" />
      </li>
      <li class="carousel__item">
        <img :src="carouselImg5" alt="" />
      </li>
    </ul>
    <div class="carousel__prev">
      <label for="carousel-1"></label>
      <label for="carousel-2"></label>
      <label for="carousel-3"></label>
      <label for="carousel-4"></label>
      <label for="carousel-5"></label>
    </div>
    <div class="carousel__next">
      <label for="carousel-1"></label>
      <label for="carousel-2"></label>
      <label for="carousel-3"></label>
      <label for="carousel-4"></label>
      <label for="carousel-5"></label>
    </div>
    <div class="carousel__nav">
      <label class="carouselNav" for="carousel-1"></label>
      <label class="carouselNav" for="carousel-2"></label>
      <label class="carouselNav" for="carousel-3"></label>
      <label class="carouselNav" for="carousel-4"></label>
      <label class="carouselNav" for="carousel-5"></label>
    </div>
  </section>
</template>

<script>
import ButtonsSocial from "@/components/ButtonsSocial.vue";
export default {
  name: "Carousel",
  components: {
    ButtonsSocial,
  },
  data: function () {
    return {
      title: "All New Phones",
      sale: "up to 25% Flat Sale",
      details:
        "It is a long established fact that a reader will be distracted by the readable content <br />of a page when looking at its layout. The point of using Lorem Ipsum is that",
      carouselImg1: require("../images/carousel/1.jpg"),
      carouselImg2: require("../images/carousel/2.jpg"),
      carouselImg3: require("../images/carousel/3.png"),
      carouselImg4: require("../images/carousel/4.jpg"),
      carouselImg5: require("../images/carousel/5.jpg"),
    };
  },
};
</script>
<style scoped lang="scss">
@media (max-width:361px){
  div h1{
    font-size: 16px;
  }

}
section {
  background-color: red;
  height: 550px;
  position: relative;
  margin-bottom: 50px;
}
.carousel-caption {
  color: red;
  font-style: bold;
}
h1 {
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
  font-size: 45px;
  line-height: 20px;
  display: block;
}
p {
  font-weight: 550;
  font-size: 18px;
  line-height: 20px;
}
.buyNow {
  text-transform: uppercase;
  background: white;
  width: 196px;
  padding: 12px 0;
  display: inline-block;
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  color: red;
  text-align: center;
  border-radius: 30px;
  text-decoration: none;
}
.buyNow:hover {
  background-color: red;
  color: white;
}
img {
  width: 100%;
  height: 100%;
}
@media (max-width: 640px) {
  section {
    height: 325px;
  }
  .carousel-caption {
    text-align: left;
    position: absolute;
    bottom: 80px;
  }
  h1 {
    font-size: 30px;
  }
  p {
    display: none;
  }
  .buyNow {
    width: 40%;
    font-size: 12px;
  }
}
@media (min-width: 640.0000001px) and(max-width: 990px) {
  section {
    height: 440px;
  }
  .carousel-caption {
    text-align: left;
    position: absolute;
    bottom: 115px;
  }
  p {
    display: none;
  }
}
@media (min-width: 990.000001px) {
  section {
    height: 550px;
  }
  .carousel-caption {
    text-align: left;
    position: absolute;
    bottom: 130px;
  }
}
label:hover {
  background-color: red;
}
.carouselNav:hover {
  opacity: 0.45;
}

%animation-default {
  opacity: 1 !important;
  z-index: 3;
}
@mixin carousel($items, $animation: "default") {
  .carousel {
    width: 100%;
    position: relative;
    overflow: hidden;

    > input[type="radio"] {
      position: absolute;
      left: 0;
      opacity: 0;
      top: 0;

      &:checked {
        ~ .carousel__items .carousel__item,
        ~ .carousel__prev > label,
        ~ .carousel__next > label {
          opacity: 0;
        }
      }

      @for $i from 1 through $items {
        &:nth-child(#{$i}) {
          &:checked {
            ~ .carousel__items .carousel__item {
              @if $animation == "default" {
                &:nth-child(#{$i}) {
                  opacity: 1;
                }
              }
            }

            ~ .carousel__prev {
              > label {
                @if $i == 1 {
                  &:nth-child(#{$items}) {
                    @extend %animation-default;
                  }
                } @else if $i == $items {
                  &:nth-child(#{$items - 1}) {
                    @extend %animation-default;
                  }
                } @else {
                  &:nth-child(#{$i - 1}) {
                    @extend %animation-default;
                  }
                }
              }
            }

            ~ .carousel__next {
              > label {
                @if $i == $items {
                  &:nth-child(1) {
                    @extend %animation-default;
                  }
                } @else {
                  &:nth-child(#{$i + 1}) {
                    @extend %animation-default;
                  }
                }
              }
            }

            ~ .carousel__nav {
              > label {
                &:nth-child(#{$i}) {
                  background: red;
                  cursor: default;
                  pointer-events: none;
                }
              }
            }
          }
        }
      }
    }

    &__item {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      transition: opacity 2s;
      -webkit-transition: opacity 2s;

      img {
        width: 100%;
        vertical-align: middle;
      }
    }

    &__prev,
    &__next {
      > label {
        border: 2px solid #fff;
        border-radius: 50%;
        cursor: pointer;
        display: block;
        width: 40px;
        height: 40px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        opacity: 0;
        z-index: 2;

        &:hover,
        &:focus {
          opacity: 0.5 !important;
        }

        &:before,
        &:after {
          content: "";
          position: absolute;
          width: inherit;
          height: inherit;
        }

        &:before {
          background: linear-gradient(
              to top,
              #fff 0%,
              #fff 10%,
              rgba(51, 51, 51, 0) 10%
            ),
            linear-gradient(to left, #fff 0%, #fff 10%, rgba(51, 51, 51, 0) 10%);
          width: 60%;
          height: 60%;
          top: 20%;
        }
      }
    }

    &__prev {
      > label {
        left: 2%;

        &:before {
          left: 35%;
          top: 20%;
          transform: rotate(135deg);
          -webkit-transform: rotate(135deg);
        }
      }
    }

    &__next {
      > label {
        right: 2%;

        &:before {
          left: 10%;
          transform: rotate(315deg);
          -webkit-transform: rotate(315deg);
        }
      }
    }

    &__nav {
      position: absolute;
      bottom: 3%;
      left: 0;
      text-align: center;
      width: 100%;
      z-index: 3;

      > label {
        border: 1px solid #fff;
        display: inline-block;
        border-radius: 50%;
        cursor: pointer;
        margin: 0 0.125%;
        width: 20px;
        height: 20px;
      }
    }
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

@include carousel(5);
</style>
